body {
  position: relative;
  margin: 0;
  min-height: 100vh;
  padding-right: 0 !important;
  color: #fff;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  font-family: 'Roboto', sans-serif;
  overflow: hidden;
}
::-webkit-input-placeholder {
  font-size: 16px;
}
::-moz-placeholder {
  font-size: 16px;
}
:-ms-input-placeholder {
  font-size: 16px;
}
:-moz-placeholder {
  font-size: 16px;
}
.main {
  min-height: 100vh;
  overflow: hidden;
  background: url(../images/wallHero5.jpg) no-repeat center / cover;
}
.header {
  position: fixed;
  top: 15px;
  width: 100%;
  z-index: 10;
}
.header__top {
  padding: 15px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__list {
  display: flex;
}
.header__link {
  margin-right: 15px;
}
.hero {
  position: relative;
  overflow: hidden;
}
.hero__box {
  height: 100vh;
  min-height: 660px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: all .3s linear;
}
.hero__content {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 10;
  perspective: 700px;
}
.hero__title {
  max-width: 700px;
  font-size: 48px;
  line-height: 1.5;
  font-weight: 900;
  perspective: 700px;
  transform: rotateY(8deg);
  text-transform: uppercase;
}
.hero__img {
  width: 300px;
  height: 300px;
  fill: #fff;
  object-fit: cover;
}
.hero__animation {
  position: absolute;
  width: 100vw;
  height: 100%;
  left: 0;
  top: 0;
}
.hero__animation img {
  display: none;
}
.hero #canvas {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.letter {
  display: inline-block;
  transform: translateY(0);
  transition: transform .3s linear;
  min-width: .2em;
}
.letter:hover {
  cursor: pointer;
  transform: translateY(-15px);
}
.remodal-first {
  padding-top: 95px;
  text-align: center;
  width: 376px;
}
@media only screen and (min-width: 320px) {
  .remodal-first {
    max-width: 376px !important;
    height: auto;
    padding: 40px 15px;
    margin: 0 auto;
    text-align: center;
    background: #f9fbfc;
    border-radius: 10px;
  }
}
@media only screen and (min-width: 768px) {
  .remodal-first {
    padding: 50px 15px;
    max-width: 489px !important;
    height: 487px;
    text-align: left;
    border-radius: 0px;
    background: url('../images/form.png') no-repeat top center;
    padding: 120px 0 0 109px;
  }
}
@media only screen and (min-width: 320px) {
  .remodal-first .remodal-close {
    color: #000 !important;
  }
}
@media only screen and (min-width: 768px) {
  .remodal-first .remodal-close {
    color: #fff !important;
  }
}
.remodal-first:before {
  display: none;
}
.btn-remodal {
  margin: 30px 0 0 15px;
}
.remodal-close {
  top: 12px !important;
  right: 15px !important;
  color: #fff !important;
  font-weight: 900;
}
@media only screen and (min-width: 320px) {
  .remodal-close {
    width: 10px !important;
    height: 16px !important;
  }
}
@media only screen and (min-width: 768px) {
  .remodal-close {
    width: 35px !important;
    height: 30px !important;
  }
}
@media only screen and (min-width: 320px) {
  .remodal-close:before {
    font-size: 30px !important;
    top: -7px !important;
    left: -12px !important;
  }
}
@media only screen and (min-width: 768px) {
  .remodal-close:before {
    font-size: 50px !important;
    top: 0px !important;
    left: 0px !important;
  }
}
.remodal-common {
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  border-radius: 15px;
  background: #fff;
  padding: 30px 40px;
}
@media only screen and (min-width: 320px) {
  .remodal-common {
    max-width: 500px !important;
    padding: 40px 15px;
    margin: 0 auto;
    text-align: center;
  }
}
@media only screen and (min-width: 768px) {
  .remodal-common {
    padding: 40px 20px;
    max-width: 450px !important;
    text-align: center;
  }
}
.remodal-common .remodal-close {
  color: #000 !important;
}
.error-input {
  clear: both;
  float: left;
  font-size: 13px;
  padding-left: 93px;
  color: #df3a3a;
  margin: 0 auto;
  text-align: center;
}
#name-error {
  font-size: 0 !important;
}
.input {
  position: relative;
}
label {
  position: absolute;
  top: 0;
  border: 0;
  font-size: 0 !important;
  vertical-align: baseline;
  margin: 0;
  padding: 0;
}
.formBox {
  position: relative;
  background: #fff;
  padding: 30px 15px;
  max-width: 400px;
  margin: 20px auto;
  text-align: center;
}
.formBox .form__line {
  width: 100%;
  padding-left: 15px;
  padding-bottom: 7px;
  border: none!important;
  outline: none;
  font-size: 18px;
}
.formBox .btn {
  margin: 15px 0 5px 0;
  color: #fff;
}
.formBox .btn:hover {
  color: #000;
}
.video {
  max-width: 780px;
}
.video iframe {
  border: none;
  width: 100%;
  height: 100%;
}
.thanks__title {
  padding-bottom: 15px;
  font-size: 22px;
  font-weight: 700;
  color: #000;
  text-transform: uppercase;
}
.thanks__text {
  display: block;
  padding-bottom: 15px;
  padding-left: 0;
  font-size: 18px;
  color: #000;
}
@keyframes arrow-bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-6px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes smoothScroll {
  0% {
    opacity: 0;
    transform: translateY(-40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes antiSmoothScroll {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-40px);
  }
}
@keyframes pulsed {
  0% {
    /* box-shadow: 0 0 0 0 rgba(204,169,44, 0.4); */
    transform: scale(1);
  }
  70% {
    /* box-shadow: 0 0 0 10px rgba(204,169,44, 0); */
    transform: scale(1.05);
  }
  100% {
    /* box-shadow: 0 0 0 0 rgba(204,169,44, 0); */
    transform: scale(1);
  }
}
@keyframes growLine {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
.slick-prev,
.slick-next {
  width: 50px;
  height: 100%;
  opacity: 1;
  transition: opacity .3s linear;
  z-index: 10;
}
.slick-prev {
  top: 152px;
  left: -50px;
}
.slick-next {
  top: 213px;
  left: -50px;
}
.slick-prev:before,
.slick-next:before {
  width: 0;
  height: 0;
}
.slick-prev:focus,
.slick-next:focus {
  opacity: 1;
}
.slick-disabled {
  opacity: .35!important;
}
.slick-dots {
  display: inline-block !important;
  left: -48px;
  top: 50%;
  transform: translateY(17%);
  border-image: transparent;
}
.slick-dots li {
  margin: 4px 8px;
  margin-bottom: 10px;
  width: 13px;
  height: 13px;
}
.slick-dots li button {
  background: #fff;
  display: block;
  height: 13px;
  width: 13px;
  font-size: 0px;
  color: #fff;
  border-radius: 50%;
  box-shadow: 0px 5px 7px 1px rgba(0, 45, 86, 0.15);
  opacity: 0.75;
}
.slick-dots li button:before {
  font-size: 0;
  opacity: 0;
  display: none;
}
.slick-dots li.slick-active button {
  background: #fb0035;
  border-radius: 50%;
  opacity: 1;
}
@media only screen and (min-width: 768px) {
  .slick-dots li.slick-active button {
    background: #fff;
  }
}
.slick-current,
.slick-slide,
.slick-active {
  outline: none!important;
}
